import {
  fetchItem,
  fetchItems
} from '../api'
import axios from 'axios'
export default {
  GETMONEY({commit, state}) {
    var num = 1
    function getprive(num){
      var url = state['apiurl'+num] + '/api/v1/website/price';
      axios({
        method: 'get',
        url: url,
      }).then(function (res) {
        console.log(res.data)
        commit("COMMITMONEY", res.data);
      }).catch(function(error){
        num +=1
        getprive(num)
      })
    }
    console.log(num)
    getprive(num)
  },
  GETNEWSITEM({commit, state}) {
    $.getJSON('./static/json/news/newsItem.json',function(data){
      const lang = state.lang;
      commit("GETNEWSITEM",data[lang]);
    })

  },
  PUBLICSITEM({commit, state}) {
    $.getJSON('./static/json/publics/publics_item.json',function(data){
      const lang = state.lang;
      commit("PUBLICSITEM",data[lang]);
    })
  },
  PUBLICMESSAGE:({commit,state},{id,Vue})=>{
    const url = "./static/json/publics/items/publics_"+id+".json";
    const that = Vue
    console.log(that)
    console.log(id)
    $.getJSON(url,function(data){
      const lang = state.lang;
      console.log (data[lang])
      if (data[lang]){
        commit("PUBLICMESSAGE",data[lang]);
      }else {
        that.$router.push({"path":"/publics"});
      }
    });
  },
  GETINDEXFRIENDS({commit, state}) {
    $.getJSON('./static/json/index/friends.json',function(data){
      commit("GETINDEXFRIENDS",data);
    })

  },
  GETJOINJSON({commit,state}){
    $.getJSON('./static/json/join/join.json',function(data){
      commit("GETJOINJSON",data);
    })
  }
}



