<template>
  <div class="rol index-public-rol" v-if="open&&publicsItem">
    <div class="m-box">
      <div class="rol index-public-box">
        <div class="index-public">
          <i class="lt fa public-icon">&#xe904;</i>
          <span class="lt public-name">{{publicsItem[0].title}}</span>
          <a href="#" class="lt public-btn"   @click="itemClick(publicsItem[0].id)">{{publics.readMore}}</a>
          <img class="public-close-btn" src="../../../assets/image/icon_off.png" @click="closePublic" alt="">

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index-public",
    data(){
      return{
        open:true
      }
    },
    created(){
      this.$store.dispatch("PUBLICSITEM");
    },
    methods:{
      closePublic:function(){
        this.open=false;
      },
      itemClick(id){
        this.$router.push({path:'/newsMessage',query:{"id":id}});
      }
    },
    computed:{
      publicsItem(){
        return this.$store.state.publicsItem;
      },
      lang(){
        return this.$store.state.lang;
      },
      publics(){
        return this.$t('publics');
      }
    },
    watch:{
      lang(){
        this.$store.dispatch("PUBLICSITEM");
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .index-public-rol{
    height:60px;
    @include property(background, #F5F7FB,#fff);
    @include property(box-shadow, 0 1px 5px 0 rgba(0,0,0,0.10),none);
    @include property(margin-bottom, 50px,30px);
  }
  .index-public-box{
    text-align: center;
    position:relative;
    margin-left:-20px;
  }
  .index-public{
    display:inline-block;
    line-height:60px;
  }
  .public-icon{
    @include property(font-size, 20px,28px);
    @include property(line-height, 60px,64px);
    @include property(margin-right, 25px,20px);
    color:#c8c8c8;
  }
  .public-name{
    @include property(font-size, 16px,25px);
    @include property(max-width, 600px,400px);
    overflow: hidden;
    height:60px;
    color:#333;
    margin-right:20px;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .public-btn{
    @include property(font-size, 16px,25px);
    color:#376AD4;
  }
  .public-close-btn{
    @include property(width, 20px,28px);
    @include property(height, 20px,28px);
    @include property(top, 23px,15px);
    @include property(right, 30px,15px);
    position:absolute;
    cursor:pointer;
    display:block;
  }
</style>


