<template>
  <div class="rol message-rol" v-if="publicMessage">
    <div class="m-box">
      <div class="rol message-body">
        <div class="rol message-title-rol">
          <div class="rol message-title">
            {{publicMessage.title}}
          </div>
          <div class="rol message-time">
            <i class="fa lt time-icon">&#xe712;</i>
            <div class="lt time-font">{{publicMessage.data}}</div>
          </div>
        </div>
        <div class="rol message-body-rol">

          <div class="rol" v-for="item in publicMessage.message">
            <p class="rol message-body-font" v-if="item.type==0" v-html="item.data"></p>

            <div class="rol message-img-box" v-if="item.type==1">
              <img :src="item.image" alt="">
              <div class="rol message-img-name" v-if="item.data">{{item.data}}</div>
            </div>
            <p class="message-body-title" v-if="item.type==2" v-html="item.data"></p>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "message_body",
    created(){
      const id = this.$route.query.id;
      const that = this
      this.$store.dispatch("PUBLICMESSAGE",{id,that});
    },
    computed:{
      publicMessage(){
        return this.$store.state.publicMessage;
      },
      lang(){
        return this.$store.state.lang;
      }
    },
    watch:{
      lang(newvalue){
        const id = this.$route.query.id;
        const that = this
        this.$store.dispatch("PUBLICMESSAGE",{id:id, Vue:that});
        // if(this.publicMessage){
        //   this.$router.push({"path":"/publics"});
        // }
      },
    }
  }
</script>

<style lang="scss">
  @import "../../../style/pie";
  .message-rol{
    @include property(padding-top, 80px,120px);
  }
  .message-body{
    margin-top:20px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    @include property(padding, 60px 120px,30px 30px);
    margin-bottom:100px;
  }
  .message-title-rol{
    padding-bottom:25px;
    border-bottom:1px dashed #e4e4e4;
  }
  .message-title{
    font-size:36px;
    color:#333;
    font-weight:bold;
    margin-bottom:30px;
    line-height:1.6em;
  }
  .time-icon{
    @include property(font-size, 16px,30px);
    color:#999;
    margin-right:12px;
  }
  .time-font{
    @include property(font-size, 14px,26px);
    color:#999;
    @include property(line-height, 18px,32px);
  }
  .message-body-rol{
    padding-top:25px;
    a{
      color:#2d49c6 !important;
      cursor: pointer;
    }
    .import{
      color:red !important;
    }
  }
  .message-body-font{
    @include property(font-size, 16px,29px);
    color:#333;
    line-height:2em;
    margin-bottom:20px;
  }
  .message-img-box{
    margin-bottom:20px;
    text-align: center;
  }
  .message-img-box img{
    max-width:100%;
  }
  .message-img-name{
    text-align:center;
    @include property(font-size, 16px,30px);
    color:#666;
    margin-top:10px;
  }
  .message-body-title{
    @include property(font-size, 20px,32px);
    color:#333;
    margin-bottom:20px;
    font-weight:700;
  }
</style>


