exports.install = function(Vue, options) {


    Vue.prototype.$isPc = () => {
        var userAgentInfo = navigator.userAgent;
        var Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"];
        var flag = true;
        for (var v = 0; v < Agents.length; v++) {
            if (userAgentInfo.indexOf(Agents[v]) > 0) {
                flag = false;
                break;
            }
        }
        return flag;
    };
  Vue.prototype.$getLang = () => {
    var lang = navigator.language||navigator.userLanguage;//IE
    lang = lang.substr(0, 2);//lang2
    if (lang == 'zh'){
      lang = 'cn';
    }
    if (lang != 'cn' && lang != 'en' && lang != 'jp'){
      lang = 'en'
    }
    return lang;
  };
  Vue.prototype.$setStorage = (name,data) => {
    window.localStorage.setItem(name,data);
  };
  Vue.prototype.$getStorage = (name) => {
    return window.localStorage.getItem(name);
  };
  Vue.prototype.$bodyScroll = (isPin) => {
    if(isPin){

      document.body.style.height = '100vh'

      document.body.style['overflow-y'] = 'hidden'
    }

    else{

      document.body.style.height = 'unset'

      document.body.style['overflow-y'] = 'auto'

    }
  };
  Vue.prototype.$getQueryString = (name) => {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) return unescape(r[2]);
    return null;
  };
  Vue.prototype.$downloadUrl = (that) => {
    if(that.$route.query.channel && that.$route.query.channel === 'coinbnn'){
      that.$store.commit('CHANGEDOWNLOADURL',true)
    }
  }
};


