import {
    createAPI
} from './create-api-client'
import axios from 'axios'
import {
    Loading
} from 'element-ui';
const logRequests = !!process.env.DEBUG_API
const api = createAPI()

function fetch(request) {
    const cache = api.cachedItems
    var loading
    const isLoading = request.header.isLoading
    if (request.header.isLoading) {
        loading = Loading.service({
            lock: true,
            text: 'Loading',
            background: 'rgba(0, 0, 0, 0.7)'
        })
    }
    if (cache && cache.has(request.header.url) && request.header.isCache) {
        return Promise.resolve(cache.get(request.header.url))
    } else {
        if (request.header.lan == 'cn') {
            request.header.lan = 'zh_CN'
        }
        return new Promise((resolve, reject) => {
            axios({
                method: request.header.method,
                url: request.header.url,
                data: request.data,
                params: request.params,
                headers: {
                    'Accept-Language': request.header.lan,
                    'Content-Type': request.header.isFormData ? 'multipart/form-data' : 'application/json; charset=utf-8'
                }
            }).then(res => {
                if (isLoading) loading.close()
                const val = res.data
                if (val && request.header.isCache) {
                    val.__lastUpdated = Date.now()
                    cache && cache.set(request.header.url, val)
                }
                resolve(val)
            }).catch((error) => {
                if (isLoading) loading.close()
                reject(error)
            })
        })
    }

}

export function fetchItem(request) {
    return fetch(request)
}

export function fetchItems(requests) {
    return Promise.all(requests.map(request => fetchItem(request)))
}


