<template>
  <div class="rol friends-rol">
    <div class="m-box">
      <div class="rol friends-title">
        {{indexFriends.title}}
      </div>
      <div class="rol phone-box">
        <a v-for="item in friends" :href="item.link" target="_blank" class="lt friends-logo">
          <img :src="item.src" alt="">
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_friends",
    created(){
      this.$store.dispatch("GETINDEXFRIENDS");
    },
    computed:{
      friends(){
        return this.$store.state.indexFriends;
      },
      indexFriends(){
        return this.$t('indexFriends');
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .friends-rol{
    background:#fafafa;
    padding:80px 0;

  }
  .friends-title{
    @include property(font-size, 36px,42px);
    @include property(font-weight, 400,500);
    color:#333;
    margin-bottom:60px;
    text-align: center;
  }
  .friends-logo{
    margin-right:87px;
    margin-bottom:30px;
    width:110px;
    height:38px;
    cursor:pointer;
    img{
      width:100%;
      height:100%;
    }
    &:nth-child(6n){
      margin-right:0;
    }
  }

  @media (max-width:1200px) {
    .friends-logo{
      width:110px;
      height:38px;
      margin-right:60px;
      &:nth-child(6n){
        margin-right:60px;
      }
      &:nth-child(4n){
        margin-right:0;
      }
    }
    .phone-box{
      padding:0 30px;
    }
  }
</style>


