<template>
  <div class="rol">
    <div class="rol header-rol" :class="{'on':scroll||topchange,'phone':isPhone}">
      <div class="m-box">
        <router-link to="/">
          <div class="lt logo-box" v-if="lang=='cn'">
            <img src="./../../../assets/image/header_logo.png" alt="logo" class="lt logo" v-if="!scroll&&!topchange">
            <img src="./../../../assets/image/bitpie_logo_blue.png" alt="logo" class="lt logo" v-if="scroll||topchange">
          </div>
          <div class="lt logo-box" v-if="lang=='en'">
            <img src="./../../../assets/image/logo_en_w@2x.png" alt="logo" class="lt logo" v-if="!scroll&&!topchange">
            <img src="./../../../assets/image/logo_en_b@2x.png" alt="logo" class="lt logo" v-if="scroll||topchange">
          </div>
        </router-link>
        <div class="lt header-i18n-box phone-box">
          <span @click="open_i18n">
            <span v-if="lang=='cn'"></span>
            <span v-if="lang=='en'">English</span>
            <i class="fa i18n-phone-icon">&#xe600;</i>
          </span>
          <div class="header-i18n-phone-box" v-if="phone_i18n_open">
            <a class="header-i18n-btn" :class="{'on':lang=='en'}" @click="langChang('en')">English</a>
            <a class="header-i18n-btn" :class="{'on':lang=='cn'}" @click="langChang('cn')"></a>
            <a href="http://bitpie.co.jp/" class="header-i18n-btn" :class="{'on':lang=='jp'}"></a>
          </div>
        </div>
        <div class="gt header-nav-box">
          <i class="fa  fa-nav-btn" @click="phone_btn" v-if="isPhone">&#xe900</i>
          <div class="gt header-nav" :class="{'on':phone_nav}">
            <router-link to="/" class="lt header-link" :class="{'on':path=='/'}" v-html="nav.index"></router-link>
            <router-link to="/publics"  :class="{'on':path=='/publics'}" class="lt header-link" v-html="nav.notice"></router-link>
            <div href="#" class="lt header-link header-first-btn" v-if="!isPhone">
              {{nav.download}}
              <div class="header-sec-btn-box"  :class="{'cn':lang=='cn'}">
                <div class="position-op-box"></div>
                <div @click="todownload()" :class="{'on':path=='/down'}" class="rol header-sec-btn">{{nav.downBtn1}}</div>
                <a href="https://bithd.com/" target="_blank"  class="rol header-sec-btn">{{nav.downBtn2}}</a>
              </div>
            </div>
            <div class="header-sec-phone-btn" v-if="isPhone">
              <router-link to="/down"  :class="{'on':path=='/download'}"  class="rol header-link">{{nav.downBtn1}}</router-link>
              <a href="https://bithd.com/" target="_blank"  class="rol header-link">{{nav.downBtn2}}</a>
            </div>
            <a href="http://hk.mikecrm.com/cxwbYRF" class="lt header-link" v-html="nav.business" target="_blank"></a>
            <router-link to="/news"  :class="{'on':path=='/news'}"   href="#" class="lt header-link" v-html="nav.news"></router-link>
            <router-link to="/join"  :class="{'on':path=='/join'}"  class="lt header-link" v-html="nav.join"></router-link>
            <a href="http://docs.bitpie.com/zh_CN/latest/" class="lt header-link" target="_blank" v-html="nav.help" v-if="lang=='cn'"></a>
            <a href="http://docs.bitpie.com/en/latest/" class="lt header-link" target="_blank" v-html="nav.help" v-if="lang=='en'"></a>
            <div class="lt header-i18n-box pc-box">
              <a class="header-i18n-btn" :class="{'on':lang=='en'}" @click="langChang('en')">EN</a>
              <a class="header-i18n-btn" :class="{'on':lang=='cn'}" @click="langChang('cn')"></a>
              <a href="http://bitpie.co.jp/" class="header-i18n-btn" :class="{'on':lang=='jp'}"></a>
            </div>
          </div>

        </div>
      </div>
    </div>
    <div class="header-nav-bg"  @click="close_nav" :class="{'on':phone_nav}"></div>
  </div>

</template>

<script>
  export default {
    name: "index_header",
    data(){
      return{
        phone_nav:false,
        phone_i18n_open:false
      }
    },
    props:{
      topchange:Boolean
    },
    computed:{
      nav(){
        return this.$t('nav');
      },
      bodyWidth(){
        return this.$store.state.bodyWidth;
      },
      isPhone(){
        return this.bodyWidth<=1185;
      },
      scroll(){
        var scroll=this.$store.state.scrollTop;
        return scroll>0
      },
      lang(){
        return this.$store.state.lang;
      },
      path(){
        console.log(this.$route.path);
        return this.$route.path;
      }

    },
    methods:{
      phone_btn:function(){
        this.phone_i18n_open=false;
        this.phone_nav?this.phone_nav=false:this.phone_nav=true;
      },
      close_nav:function(){
        this.phone_nav=false;
      },
      langChang:function(lang){
        this.$store.commit("LANG",lang);
        this.$i18n.locale=lang;
        // this.$router.push({ params: { lang: lang }});
        this.$setStorage("lang",lang);
        this.phone_i18n_open=false;
      },
      open_i18n:function(){
        this.phone_i18n_open?this.phone_i18n_open=false:this.phone_i18n_open=true;
        this.phone_nav=false
      },
      todownload:function(){
        if(this.$route.query.channel && this.$route.query.channel === 'coinbnn'){
          this.$router.push({path:'/down',query:{'channel': 'coinbnn'}})
        }else{
          this.$router.push({path:'/down'})
        }

      }
    },
    created(){
      var that = this;
      $(document).click(function(e){
        var _con = $('.header-i18n-phone-box');   // 
        if(!_con.is(e.target) && _con.has(e.target).length === 0&&!($(e.target).hasClass('header-i18n-box')||$(e.target).parents().hasClass('header-i18n-box'))){
          that.phone_i18n_open=false
        }
      });
      window.onscroll= function(){
        that.phone_i18n_open=false
      }
    }


  }
</script>
<style scoped lang="scss">
  @import "../../../style/pie";
  .header-rol{
    @include property(height, 60px,100px);
    position:fixed;
    top:0;
    left:0;
    z-index: 20000;
    @include property(width, 100%,750px);
    &.on{
      background:#f8f8f8;
      border-bottom:1px solid #e7e7e7;
      /*font-weight:bold;*/
      .header-link{
        color:#2d49c6;
        &:hover{
          @include property(background, #2d49c6,#f8f8f8);
          @include property(color, #fff,#2d49c6);
        }
        &.on{
          @include property(background, #2d49c6,#f8f8f8);
          @include property(color, #fff,#2d49c6);
        }
      }
      .header-i18n-box{
        @include property(border, 1px solid  rgba(45,73,198,0.30),1px solid transparent);
        @include property(background,rgba(45,73,198,0.04),none);
        position:relative;

      }
      .header-i18n-btn{
        color:#2d49c8;
        cursor:pointer;
        &:hover{
          border-bottom:2px solid #2d49c8;
        }
        &.on{
          border-bottom:2px solid #2d49c8;
        }
      }
      .fa-nav-btn{
        color:#777;
      }
      .header-i18n-box.phone-box{
        position:absolute;
        top:20px;
        right:100px;
        width:160px !important;
        height:40px;

        background:none;
        span{
          font-size:28px;
          color:#777 !important;
          line-height:40px;
        }
        i{
          font-size:28px;
          color:#777 !important;
          line-height:40px;
        }

      }

    }
    &.phone{
      .header-nav{
        position:absolute;
        top:100px;
        left:0;
        width:750px;
        background:#f8f8f8;
        padding-bottom:30px;
        display:none;
        &.on{
          display:block;
        }
      }
      .header-link{
        width:100%;
        color:#777;
        font-size:26px;
        height:50px;
        line-height:50px;
        padding-left:50px;
      }
      .header-i18n-box{
        @include property(border, 1px solid #777,1px solid transparent);
        margin-left:15px;
        width:100%;
      }
      .header-i18n-btn{
        width:100%;
        display:block;
        text-align: center;
        color:#333;
        font-size:30px;
        line-height:2em;
      }
    }
  }
  .logo{
    @include property(height, 38px,50px);
    @include property(width, 145px,190px);
    @include property(margin-top, 12px,25px);
  }
  .fa-nav-btn{
    position:absolute;
    width:30px;
    height:30px;
    top:25px;
    right:40px;
    font-size:50px;
    color:#fff;
    cursor: pointer;
  }
  .header-link{
    padding:0 15px;
    margin-right:5px;
    height:30px;
    margin-top:15px;
    font-size:16px;
    line-height:30px;
    color:#fff;
    border-radius:30px;
    &:last-child{
      margin-right:0;
    }
    &:hover{

      background:#fff;
      color:#3B2CC1;
    }
    &.on{
      background:#fff;
      color:#3B2CC1;
    }

  }
  .header-i18n-box{
    width:135px;
    @include property(width, 140px,200px);
    height:30px;
    margin-top:15px;
    border-radius:30px;
    border:0.5px solid rgba(255,255,255,0.5);
    @include property(padding, 0 20px,0);
    background:rgba(255,255,255,0.05);
  }
  .header-i18n-btn{
    color:#fff;
    line-height:30px;
    font-size:12px;
    margin-right:3px;
    padding-bottom:1px;
    border-bottom:2px solid transparent;
    &:last-child{
      margin-right:0;
    }
    &:hover{
      border-bottom:2px solid #fff;
    }
    &.on{
      border-bottom:2px solid #fff;
    }
  }
  .header-nav-bg{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.5);
    position:fixed;
    top:0;
    left:0;
    z-index:10000;
    display:none;
    &.on{
      display:block;
    }
  }
  .pc-box{
    @include property(display,block,none);
  }
  .phone-box{
    @include property(display,none,block);
  }
  .header-i18n-phone-box{
    position:absolute;
    top:70px;
    left:-40px;
    width:200px;
    height:235px;
    padding:10px 0;
    border-radius:8px;
    background:url('../../../assets/image/i18n-bg.png') no-repeat;
    background-size:100% 100%;
    padding-top:25px;
    box-sizing: border-box;
    .header-i18n-btn{
      width:100px;
    }
  }
  .header-i18n-box.phone-box{
    position:absolute;
    top:20px;
    right:100px;
    width:160px !important;
    height:40px;

    background:none;
    span{
      font-size:28px;
      color:#fff !important;
      line-height:40px;
    }
    i{
      font-size:28px;
      color:#fff !important;
      line-height:40px;
    }
    .header-i18n-btn{
      color:#888;
      cursor:pointer;
      &:hover{
        color:#2d49c8;
        border-bottom:none;
      }
      &.on{
        color:#2d49c8;
        border-bottom:none;
      }
    }
  }
  .downloadBg{
    top:-9px;
    width:24px;
    height:10px;
    right:20px;
    position:absolute;
  }
  .header-first-btn{
    position:relative;
    cursor:pointer;
    &:hover{
      .header-sec-btn-box{
        display:block;
      }
    }
  }
  .header-sec-btn-box{
    @include property(position,absolute,static);
    display:none;
    top:50px;
    left:-40px;
    width:140px;
    height:auto;
    padding:10px 20px 5px 20px;
    border-radius:5px;
    background:url('../../../assets/image/i18n-bg.png') no-repeat;
    background-size:100% 100%;
    &.cn{
      width:100px;
      padding:10px 20px 5px 20px;
      left:-25px;
    }
  }
  .header-sec-btn{
    width:100%;
    color:rgba(45,73,200,1);
    border-bottom:1px solid #a6bee1;
    height:50px;
    line-height:50px;
    text-align: center;
    &:last-child{
      border-bottom:none;
    }
    &:hover{
      color:rgba(45,73,200,0.6);
    }
  }
  .position-op-box{
    width:94px;
    position:absolute;
    height:60px;
    top:-60px;
  }
  .header-sec-phone-btn{
    .header-sec-btn{
      border-bottom:none;
      text-align: left;
      padding-left:50px;
      font-size:20px;
      color:#777;
      &.on{
        color:#2d49c8;
      }
    }
  }
</style>


