<template>
  <div class="rol news-item-rol">
    <div class="m-box">
      <div class="rol news-item-box">
        <a :href="item.url" target="_blank" class="rol news-item" v-for="item in newsItem">
          <img :src="item.img" alt="img" class="lt news-item-img">
          <div class="lt news-item-font-box">
            <div class="rol news-title" style="-webkit-box-orient: vertical;">{{item.title}}</div>
            <div class="rol news-message" style="-webkit-box-orient: vertical;">
              {{item.intro}}
            </div>
            <div class="rol news-data-rol">
              <div class="lt news-time-box">
                <i class="lt fa news-time-icon">&#xe712;</i>
                <span class="lt news-time-font">{{item.data}}</span>
              </div>
              <div class="lt news-see-box">
                <i class="lt fa news-time-icon">&#xe711;</i>
                <span class="lt news-time-font">{{item.watch}}</span>
              </div>
            </div>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "news_item",
    created(){
      this.$store.dispatch("GETNEWSITEM");
    },
    methods:{
      pageChange(currentPage){
        console.log(currentPage);
      },
    },
    computed:{
      newsItem(){
        return this.$store.state.newsItem
      },
      lang(){
        return this.$store.state.lang
      }
    },
    watch:{
      lang(){
        this.$store.dispatch("GETNEWSITEM");
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .news-item-rol{
    @include property(padding-top, 80px,120px);
  }
  .news-item-box{
    margin-top:20px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    @include property(padding, 30px 80px,0px 20px);
    @include property(margin-bottom,100px,50px);
  }
  .news-item{
    @include property(padding, 20px 20px,30px 0px);
    @include property(height, 175px,auto);
    @include property(border-bottom, none,1px dashed rgba(200,200,200,0.5));
    cursor: pointer;
    &:hover{
      @include property(background, #f5f7fb,none);
    }
    &:last-child{
      margin-bottom:0;
      border-bottom:none;
    }
  }
  .news-item-img{
    @include property(width, 245px,220px);
    @include property(height, 135px,130px);
    @include property(margin-right, 40px,20px);
  }
  .news-item-font-box{
    @include property(width, 605px,399px);
    padding-top:3px;
  }
  .news-title{
    @include property(font-size, 22px,30px);
    @include property(margin-bottom, 14px,20px);
    @include property(height, 23px,80px);
    @include property(line-height, 1em,1.4em);
    @include property(-webkit-line-clamp, 1,2);
    overflow: hidden;
    // eslint-disable-next-line
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    display: -webkit-box;
    color:#333;
    height:26px;
    line-height:26px;
  }
  .news-message{
    @include property(font-size, 13px,24px);
    @include property(margin-bottom, 25px,10px);
    @include property(height, 40px,80px);
    @include property(display, -webkit-box,none);
    color:#666;
    line-height:1.6em;
    margin-bottom:25px;
    display: -webkit-box;
    // eslint-disable-next-line
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .news-time-icon{
    @include property(font-size, 16px,26px);
    color:#999;
    margin-right:8px;
  }
  .news-time-font{
    @include property(font-size, 13px,24px);
    @include property(line-height, 16px,28px);
    color:#999;
    line-height:16px;
  }
  .news-time-box{
    margin-right:30px;
  }
  .pagination-rol{
    text-align: center;
  }
  .pagination{
    display: inline-block;
  }
</style>


