<template>
  <div class="rol">
    <index-header :topchange="true"></index-header>
    <div class="rol join-alert-message-box">
      <div class="m-box">
        <div class="rol message-body">
          <div class="rol join-alert-title">
            {{joinJson[joinPlace][joinId].name}}
          </div>
          <div class="rol join-alert-bt">{{join.Responsibility}}:</div>
          <ul class="rol join-ul join-ul-first" >
            <li v-for="item in joinJson[joinPlace][joinId].duty">{{item}}</li>
          </ul>
          <div class="rol join-alert-bt">{{join.Requirements}}:</div>
          <ul class="rol join-ul" >
            <li v-for="item in joinJson[joinPlace][joinId].need">{{item}}</li>
          </ul>
          <div class="rol join-alert-email">{{join.Send}}hr@bitpie.com </div>
          <div class="rol join-btn-rol">
            <a href="mailto:hr@bitpie.com" class="join-btn">{{join.joinBtn}}</a>
          </div>
        </div>
      </div>
    </div>
    <index-footer></index-footer>
  </div>
</template>

<script>
  import header from '../index/components/index_header';
  import indexFooter from '../index/components/index_footer';
  export default {
    name: "join_message",
    computed:{
      joinOpen (){
        return this.$store.state.joinOpen;
      },
      joinId(){
        return this.$route.query.joinId;
      },
      joinJson(){
        return this.$store.state.joinJson;
      },
      joinPlace(){
        console.log(this.$route.query.joinPlace)
        return this.$route.query.joinPlace;
      },
      join(){
        return this.$t('join')
      }
    },
    created(){
      this.$store.dispatch("GETJOINJSON");
    },
    components: {
      indexHeader: header,
      indexFooter:indexFooter
    },
  }
</script>

<style scoped lang="scss">
  @import "../../style/pie";
  .join-alert-box{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    z-index: 20000;
  }
  .join-alert-message-box{
  @include property(width, 620px,100%);
  @include property(height, auto,auto);
  @include property(padding-top, 0,160px);
    box-sizing: border-box;
    border-radius:7px;
  }
  .join-alert-title{
    font-size:30px;
  @include property(font-size, 30px,42px);
  @include property(font-weight, 400,500);
    color:#333;
    text-align: center;
    margin-bottom:30px;
    border-bottom:1px dashed #e4e4e4;
    height:100px;
    line-height:100px;
  .gt{
    font-size:30px;
    cursor:pointer;
    color:#999;
  }
  }
  .join-alert-bt{
    font-size:18px;
  @include property(font-size, 18px,32px);
  @include property(font-weight, 400,500);
    color:#2d49c6;
    margin-bottom:20px;
  }
  .join-ul{
  @include property(padding-left, 20px,0px);
    margin-bottom:40px;
  }
  .join-ul li{
    /*list-style:disc ;*/
  @include property(font-size, 14px,28px);
  @include property(line-height, 24px,52px);
    line-height:24px;
    background:url("../../assets/image/dish.png") no-repeat left 24px;
    padding-left:15px;
    background-size:6px;


  }
  .join-alert-email{
  @include property(line-height, 14px,28px);
  @include property(font-size, 16px,28px);
    color:#2d49c6;
    margin-bottom:30px;
  }
  .join-btn-rol{
    text-align: center;
  }
  .join-btn{
    @include property(font-size, 17px,32px);
    @include property(width, 185px,300px);
    @include property(height, 55px,80px);
    @include property(border-radius, 55px,80px);
    @include property(line-height, 55px,80px);
    display:inline-block;
    text-align: center;
    background-image: linear-gradient(50deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    color:#fff;
    cursor: pointer;
  }
  .message-body{
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    padding:30px;
    padding-top:0;
    margin-bottom:100px;
    box-sizing: border-box;
  }
</style>


