      },
      downloadUrl() {
        return this.$store.state.downloadUrl;
      },
      year() {
        return this.$store.state.year
      }

    },
    methods:{
      telegramClick(){
        this.telegram ? this.telegram = false : this.telegram = true;
      },
      openImg(){
        this.alertImg?this.alertImg = false : this.alertImg = true
      }
    },
    created(){
      var that = this;
      $(document).click(function(e){

        var _con = $('.telegram-box');   // 

        if(!_con.is(e.target) && _con.has(e.target).length === 0){

          that.telegram=false;

        }

      });
    }
  }
