import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex);


  const store = new Vuex.Store({
    state: {
      scrollTop:0,
      bodyWidth:document.body.clientWidth ,
      lang:"cn",
      money:null,
      apiurl1:'https://api3.bitpie.com',
      apiurl2:'https://api2.bitpie.com',
      apiurl3:'https://api1.bitpie.com',
      newsItem:null,
      publicsItem:null,
      publicMessage:null,
      indexFriends:null,
      joinJson:null,
      joinOpen:false,
      joinId:"",
      joinPlace:"bj",
      newsIndex:0,
      downloadUrl: {
        bitpie:{
          app: 'https://bitpie.com/iosdownload/',
          google: 'https://play.google.com/store/apps/details?id=com.bitpie',
          android: 'https://bitpie.com/android/'
        },
        bither:{
          ios: 'https://bitpie.com/bither/ios/',
          android: 'https://dn-bither.qbox.me/bither.apk',
          google: 'https://play.google.com/store/apps/details?id=net.bither',
          win64: 'https://dn-bither.qbox.me/Bither-windows64.exe',
          win32: 'https://dn-bither.qbox.me/Bither-windows32.exe',
          mac64: 'https://bitpie.com/bither/macos/',
          linux: 'https://dn-bither.qbox.me/Bither-linux64.tar'
        }
      }
    },
    actions,
    mutations,
    getters
  });
export default store


