<template>
  <div class="rol news-box" v-if="newsItem">
    <div class="m-box">
      <div class="rol news-header">
        <div class="rol news-title">{{news.title}}
          <div class="gt news-orther-btn" v-if="false" @click="pageChange(newsIndex)">{{news.btn}}</div>
        </div>
      </div>
      <div class="rol news-body">
        <a :href="item.url" target="_blank"  class="lt news-item-box" v-for="item in newsItem">
          <div class="news-item">
            <img :src="item.img" alt="" class="rol news-img">
            <div class="rol news-item-title" style="-webkit-box-orient: vertical;">{{item.title}}</div>
            <div class="rol news-item-message" style="-webkit-box-orient: vertical;">{{item.intro}}</div>
            <div class="gt news-item-btn">{{news.read}}</div>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_news",
    computed:{
      news(){
        return this.$t('news');
      },
      newsItem(){
       return this.$store.state.newsItem;
      },
      // newsIndex(){
      //   return this.$store.getters.GETNEWSITEM.newsIndex;
      // },
      lang(){
        return this.$store.state.lang
      }
    },
    created(){
      this.$store.dispatch("GETNEWSITEM");
    },
    methods:{
      // pageChange(newsIndex){
      //   console.log(newsIndex);
      //   this.$store.commit("NEWSINDEX",newsIndex)
      // },
    },
    watch:{
      lang(){
        this.$store.dispatch("GETNEWSITEM");
      },
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .news-box{
    /*height:536px;*/
    background:#fff;
    padding:50px 0;
  }
  .news-header{
    padding:0 10px;
    margin-bottom:10px;
    position:relative;
  }
  .news-title{
    @include property(font-size, 28px,34px);
    @include property(line-height, 30px,50px);
    color:#333;
    margin-bottom:15px;
  }
  .news-message{
    font-family: PingFangSC-Regular;
    @include property(font-size, 18px,24px);
    color:#333;
    line-height:28px;
  }
  .news-orther-btn{
    @include property(width, 95px,135px);
    @include property(height, 30px,50px);
    @include property(line-height, 30px,50px);
    @include property(border-radius, 30px,50px);
    text-align: center;
    cursor:pointer;
    @include property(font-size, 16px,25px);
    border: 1px solid transparent;
    padding:1px;
    background: -webkit-linear-gradient( left, #fff 0%, #fff 100%), -webkit-linear-gradient( left, #99d6ee 0%, #6362e1 100%);
    background-clip: content-box, padding-box;
  }
  .news-item-box{
    @include property(width, 275px,350px);
    @include property(height, 300px,410px);
    position:relative;
  }
  .news-item{
    position:absolute;
    top:10px;
    left:10px;
    box-sizing: border-box;
    padding:15px;
    transition:all 0.2s;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    @include property(width, 255px,330px);
    @include property(height, 280px,390px);
    height:303px;
    cursor:pointer;
    border-radius:3px;
    &:hover{
      transform:scale(1.05);
      border:1px solid rgba(120,202,243,0.5);

    }
  }
  .news-img{
    width:100%;
    @include property(height, 125px,170px);
    margin-bottom:28px;
  }
  .news-item-title{
    @include property(font-size, 16px,28px);
    margin-bottom:10px;
    color:#000;
    display: -webkit-box;
    @include property(height, 25px,auto);
    // eslint-disable-next-line
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: 1;
    overflow: hidden;
    line-height:1.6em;

  }
  .news-item-message{
    @include property(font-size, 12px,22px);
    @include property(height, 37px,67px);
    overflow:hidden;
    color:#666;
    line-height:1.6em;
    margin-bottom:15px;
    display: -webkit-box;
    // eslint-disable-next-line
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: 2;
    overflow: hidden;

  }
  .news-item-btn{
    cursor:pointer;
    @include property(font-size, 10px,20px);
    color:#a8a8a8;
  }
  .news-body{
    @include property(height, 300px,410px);
    overflow:hidden;
  }
</style>


