<template>
  <div class="rol three-intro" :class="{'en':lang=='en'}">
    <div class="rol intro-box">
      <div class="m-box">
        <div class="rol intro-padding-rol">
          <div class="gt intro-message-box">
            <div class="rol intro-message-title">{{intro.p1.title}}</div>
            <div class="rol intro-message-font" v-html="intro.p1.message"></div>
          </div>
          <div class="lt intro-img-box">
            <img src="../../../assets/image/pic_o@2x.png" class="intro-img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="rol intro-box">
      <div class="m-box">
        <div class="rol intro-padding-rol">
          <div class="lt intro-message-box">
            <div class="rol intro-message-title">{{intro.p2.title}}</div>
            <div class="rol intro-message-font" v-html="intro.p2.message">

            </div>
          </div>
          <div class="gt intro-img-box">
            <img src="../../../assets/image/pic_t@2x.png" class="intro-img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="rol intro-box">
      <div class="m-box">
        <div class="rol intro-padding-rol">
          <div class="gt intro-message-box">
            <div class="rol intro-message-title">{{intro.p3.title}}</div>
            <div class="rol intro-message-font" v-html="intro.p3.message"></div>
          </div>
          <div class="lt intro-img-box">
            <img src="../../../assets/image/pic_th@2x.png" class="intro-img" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "index_intro",
    computed:{
      intro(){
        return this.$t('intro');
      },
      lang(){
        return this.$store.state.lang;
      }
    }

  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .intro-box{
    @include property(height, 516px,auto);
    padding:80px 0;
    &:nth-child(odd){
      background:#fff;
      .intro-img-box{
        @include property(text-align, left,center);
      }
      .intro-message-box{
        @include property(text-align, right,left);
      }
    }
    &:nth-child(even){
      background:#fafafa;
      .intro-img-box{
        @include property(text-align, right,center);
      }
      .intro-message-box{
        @include property(text-align, left,left);
      }
    }
  }
  .intro-padding-rol{
    padding:0 28px;
  }
  .intro-img-box{
    @include property(width, 465px,100%);
  }
  .intro-message-box  {
    @include property(width, 370px,100%);
  }
  .intro-message-box{
    @include property(margin-bottom, 0,30px);
  }
  .intro-img{
    @include property(height, 393px,580px);
    width:auto;
  }
  .intro-message-title{
    font-family: PingFangSC-Regular;
    @include property(font-size, 36px,42px);
    @include property(font-weight, 400,500);
    color: #333;
    margin-bottom:30px;
  }
  .intro-message-font{
    @include property(font-size, 18px,28px);
    color: #333;
    line-height:1.6em;
    height:130px;
  }
  .three-intro.en{
    .intro-message-font{
      height:auto;
    }
  }
</style>


