<template>
  <div class="rol banner-box" :class="{'cn':lang=='cn'}">
    <div class="m-box">
      <div class="gt banner-gt">
        <div class="rol banner-title">
          {{banner.title}}
        </div>
        <div class="rol banner-message">
          {{banner.message}}
        </div>
        <div class="rol banner-download">
          <div class="center-box">
            <a :href="downloadUrl.bitpie.app" class="lt banner-download-btn">
              <i class="lt fa banner-download-icon">&#xe901;</i>
              <div class="lt download-font-box">
                <div class="rol download-font-title">Download on the</div>
                <div class="rol download-font-name">App Store</div>
              </div>
            </a>
            <a :href="downloadUrl.bitpie.android" class="lt banner-download-btn" v-if="lang=='cn'">
              <i class="lt fa banner-download-icon android-icon">&#xe902;</i>
              <div class="lt download-font-box">
                <div class="rol download-font-title">Download for</div>
                <div class="rol download-font-name">Android</div>
              </div>
            </a>
            <a :href="downloadUrl.bitpie.google" class="lt banner-download-btn" v-else>
              <i class="lt fa banner-download-icon">&#xe903;</i>
              <div class="lt download-font-box">
                <div class="rol download-font-title">Get it in</div>
                <div class="rol download-font-name">Google Play</div>
              </div>
            </a>
          </div>
        </div>
      </div>
      <div class="lt banner-phone-lt">
        <img class="lt banner-phone-img" src="../../../assets/image/banner@2x.png" alt="">
        <div class="banner-an-box">
          <img src="../../../assets/image/btc@2x.png" alt="" class="lt banner-phone-item no1">
          <img src="../../../assets/image/eth@2x.png" alt="" class="lt banner-phone-item no2">
          <img src="../../../assets/image/eos@2x.png" alt="" class="lt banner-phone-item no3">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_banner",
    computed:{
      nav(){
        return this.$t('nav');
      },
      bodyWidth(){
        return this.$store.state.bodyWidth;
      },
      lang(){
        return this.$store.state.lang;
      },
      banner(){
        return this.$t('banner');
      },
      downloadUrl() {
        return this.$store.state.downloadUrl;
      }
    }
  }

</script>

<style scoped lang="scss">
  $hoverColor:#3B2CC1;
  @import "../../../style/pie";
  .center-box{
    @include property(display, block,inline-block);
    text-align: left;
  }
  .banner-box{
    @include property(background, url("../../../assets/image/banner_bg.png") no-repeat,url("../../../assets/image/phone-banner-bg.jpg") no-repeat);

    @include property(height, 748px,1158px);
    margin-bottom:30px;
    background-size:100% 100%;
    &.cn{
      @include property(background, url("../../../assets/image/banner_bg.png") no-repeat,url("../../../assets/image/phone-banner-cn.jpg") no-repeat);
      @include property(height, 748px,1000px);
      background-size:100% 100%;
    }
  }
  .banner-gt{
    @include property(margin-top, 230px,130px);
    @include property(padding, 0,0 11px);
    @include property(width, 580px,100%);
    @include property(text-align, left,left);
  }
  .banner-title{
    font-family: PingFangSC-Regular;
    font-size:46px;
    color:#fff;
    line-height:55px;
    margin-bottom:30px;
  }
  .banner-message{
    font-family: PingFang SC;
    @include property(font-size, 16px,25px);
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 2em;
  }
  .banner-download{
    margin-top:40px;
  }
  .banner-download-btn{
    @include property(width, 162px,245px);
    @include property(height, 42px,70px);
    border:1px solid rgba(255,255,255,0.5);
    border-radius:42px;
    padding-left:18px;
    cursor:pointer;
    margin-right:20px;
    background:rgba(255,255,255,0.05);
    /*transition:all 0.1s;*/
    &:last-child{
      margin-right:0;
      @include property(width, 165px,245px);
      .download-font-box{
        @include property(width, 110px,160px);
      }
    }
    &:first-child{
      @include property(width, 165px,245px);
    }
    &:hover{
      background:#fff;
      color:#fff;
      .banner-download-icon{color:$hoverColor};
      .download-font-title{color:$hoverColor};
      .download-font-name{color:$hoverColor};
    }
  }
  .banner-download-icon{
    @include property(font-size, 24px,36px);
    color:#fff;
    @include property(line-height, 36px,62px);
  }
  .download-font-box{
    @include property(width, 103px,164px);
    margin-left:9px;
    @include property(margin-top, 4px,10px);
  }
  .download-font-title{
    font-family: PingFangSC-Regular;
    @include property(font-size, 9px,20px);
    @include property(margin-bottom, 0,2px);
    color:#fff;
    letter-spacing: 0;

  }
  .download-font-name{
    font-family: PingFangSC-Semibold;
    @include property(font-size, 18px,22px);
    color: #FFF;
    letter-spacing: 0;
    margin-top:1px;
  }
  .android-icon{
    @include property(font-size, 26px,41px);
  }
  .banner-phone-lt{
    margin-top:160px;
    @include property(width, 431px,100%);
    @include property(display, block,none);
    @include property(margin-top, 160px,25px);
    height:508px;
    position:relative;
    &:hover{


    }
  }
  .banner-phone-img{
    @include property(width, 450px,350px);
    @include property(margin-left, 0,150px);

  }
  .banner-phone-item{
    position:absolute;
    @include property(width, 201px,154px);
    &.no1{
      @include property(top, 189px,193px);
      @include property(left, 24px,20px);
      -webkit-transition: all .28s ease-in;
      transition: all .28s ease-in .1s;
    }
    &.no2{
      @include property(top, 227px,222px);
      @include property(left, 24px,20px);
      -webkit-transition: all .28s ease-in .2s;
      transition: all .28s ease-in .2s;
    }
    &.no3{
      top:265px;
      @include property(top, 265px,251px);
      @include property(left, 24px,20px);
      -webkit-transition: all .28s ease-in .35s;
      transition: all .28s ease-in .35s;
    }
  }
  .banner-an-box{
    position:absolute;
    @include property(top, 0px,-45px);
    @include property(left, 0px,150px);
    width:230px;
    height:500px;
    &:hover{
      .banner-phone-item{
        box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.1);
        &.no1{
          top:150px;
          left:-43px;
          width:250px;
        }
        &.no2{
          top:200px;
          left:-30px;
          width:240px;
        }
        &.no3{
          top:250px;
          left:-20px;
          width:240px;
        }
      }
    }
  }
</style>


