<template>
  <div class="rol join-alert-out-box" v-if="this.joinOpen">
    <div class="rol join-alert-box" @click="closeAlert"></div>
    <div class="rol join-alert-message-box">
      <div class="rol join-alert-title">
        {{joinJson[joinPlace][joinId].name}}
        <i class="fa gt" @click="closeAlert">&#xe906;</i>
      </div>
      <div class="rol join-alert-bt">{{join.Responsibility}}:</div>
      <ul class="rol join-ul join-ul-first" >
        <li v-for="item in joinJson[joinPlace][joinId].duty">{{item}}</li>
      </ul>
      <div class="rol join-alert-bt">{{join.Requirements}}:</div>
      <ul class="rol join-ul" >
        <li v-for="item in joinJson[joinPlace][joinId].need">{{item}}</li>
      </ul>
      <div class="rol join-alert-email">{{join.Send}}hr@bitpie.com </div>
      <div class="rol join-btn-rol">
        <a href="mailto:hr@bitpie.com" class="join-btn">{{join.joinBtn}}</a>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "join_alert",
    computed:{
      joinOpen (){
        return this.$store.state.joinOpen;
      },
      joinId(){
        return this.$store.state.joinId;
      },
      joinJson(){
        return this.$store.state.joinJson
      },
      joinPlace(){
        return this.$store.state.joinPlace
      },
      join(){
        return this.$t('join')
      }
    },
    methods:{
      closeAlert(){
        this.$store.commit("ALERTOPEN");
        this.$bodyScroll(0);
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .join-alert-box{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    position:fixed;
    top:0;
    left:0;
    z-index: 20000;
  }
  .join-alert-message-box{
    position:fixed;
    left:50%;
    top:50%;
    background:#fff;
    border-radious:8px;
    @include property(width, 620px,750px);
    @include property(height, auto,100%);
    @include property(padding-top, 50px,50px);
    transform: translate(-50%,-50%);
    z-index: 30000;

    padding:30px 50px;
    box-sizing: border-box;
    border-radius:7px;
  }
  .join-alert-title{
    font-size:30px;
    @include property(font-size, 30px,42px);
    @include property(font-weight, 400,500);
    color:#333;
    text-align: center;
    margin-bottom:30px;
    .gt{
      font-size:30px;
      cursor:pointer;
      color:#999;
    }
  }
  .join-alert-bt{
    font-size:18px;
    @include property(font-size, 18px,32px);
    @include property(font-weight, 400,500);
    color:#2d49c6;
    margin-bottom:20px;
  }
  .join-ul{
    @include property(padding-left, 20px,0px);
    margin-bottom:40px;
  }
  .join-ul li{
    /*list-style:disc ;*/
    @include property(font-size, 14px,28px);
    @include property(line-height, 24px,36px);
    line-height:24px;
    background:url("../../../assets/image/dish.png") no-repeat left 8px;
    padding-left:15px;
    background-size:6px;


  }
  .join-alert-email{
    @include property(line-height, 14px,28px);
    color:#2d49c6;
    margin-bottom:30px;
  }
  .join-btn-rol{
    text-align: center;
  }
  .join-btn{
    width:185px;
    height:55px;
    display:inline-block;
    text-align: center;
    line-height:55px;
    background-image: linear-gradient(50deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    border-radius:60px;
    font-size:17px;
    color:#fff;
    cursor: pointer;
  }
  .join-alert-out-box{
    width:100%;
    height:100%;
  }
</style>


