<template>
  <div class="rol banner-rol">
    <div class="m-box">
      <div class="lt join-lt">
        <div class="rol join-font" :class="{'en':lang=='en'}" v-html="join.banner"></div>
      </div>
      <div class="lt join-gt">
        <img src="../../../assets/image/join_banner_da.png" class="lt join-banner-img" alt="">
        <img src="../../../assets/image/join_banner_zuo1.png" class="join-banner-img1" alt="">
        <img src="../../../assets/image/join_banner_zuo2.png" class="join-banner-img2" alt="">
        <img src="../../../assets/image/join_banner_you1.png" class="join-banner-img3" alt="">
        <img src="../../../assets/image/join_banner_you2.png" class="join-banner-img4" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "join_banner",
    computed:{
      join(){
        return this.$t('join')
      },
      lang(){
        return this.$store.state.lang
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .banner-rol{
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    height:520px;
    @include property(height,520px,880px);
    margin-top:60px;
  }
  .banner-font{
    line-height:675px;
    text-align: center;
    font-size:36px;
    color:#fff;
  }
  .join-lt{
    width:600px;
    margin-top:100px;
    @include property(margin-top,100px,130px);
    @include property(margin-bottom,0,40px);
  }
  .join-font{
    font-size:60px;
    color:#fff;
    line-height:107px;
    @include property(line-height,107px,95px);
    @include property(font-weight,400,300);
    font-family: PingFangSC-Light;
    &.en{
      @include property(font-size,42px,42px);
      @include property(font-weight,400,400);
    }
  }
  .join-gt{
    width:500px;
    margin-top:100px;
    position:relative;
    @include property(margin-left,0px,30px);

  }
  .join-banner-img{
    width:300px;
    height:287px;
    @include property(margin-left,125px,125px);
  }
  .join-banner-img1{
    position:absolute;
    width:110px;
    height:100px;
    top:0;
    left:30px;
    &:hover{
      animation:img1 2s ease-in-out infinite alternate;
      animation-iteration-count:1;
    }
  }
  .join-banner-img2{
    position:absolute;
    width:101px;
    height:82px;
    top:280px;
    left:80px;
    &:hover{
      animation:img2 2s ease-in-out infinite alternate;
      animation-iteration-count:1;
    }
  }
  .join-banner-img3{
    position:absolute;
    width:112px;
    height:98px;
    top:-60px;
    left:400px;
    &:hover{
      animation:img3 2s ease-in-out infinite alternate;
      animation-iteration-count:1;
    }
  }
  .join-banner-img4{
    position:absolute;
    width:148px;
    height:138px;
    top:240px;
    left:460px;
    &:hover{
      animation:img4 2s ease-in-out infinite alternate;
      animation-iteration-count:1;
    }
  }
  @keyframes img1{
    0% {
      top:0;
    }
    50%{
      top:-20px;
    }
    100% {
      top:0;
    }
  }
  @keyframes img2{
    0% {
      top:280px;
    }
    50%{
      top:260px;
    }
    100% {
      top:280px;
    }
  }
  @keyframes img3{
    0% {
      top:-60px;

    }
    50%{
      top:-80px;
    }
    100% {
      top:-60px;

    }
  }
  @keyframes img4{
    0% {
      top:240px;
    }
    50%{
      top:220px;
    }
    100% {
      top:240px;
    }
  }
</style>


