<template>
  <div class="rol safe-intro-rol">
    <div class="m-box">
      <div class="lt safe-intro-box">
          <div class="safe-intro-in-box">
            <div class="rol safe-intro-title">{{safe.p1.title}}</div>
            <div class="rol safe-intro-message">
              {{safe.p1.message}}
            </div>
            <img src="../../../assets/image/icon_blue.png" class="safe-intro-icon" alt="">
          </div>
        </div>
      <div class="lt safe-intro-box">
          <div class="safe-intro-in-box">
            <div class="rol safe-intro-title">{{safe.p2.title}}</div>
            <div class="rol safe-intro-message">{{safe.p2.message}}</div>
            <img src="../../../assets/image/icon_yellow.png" class="safe-intro-icon" alt="">
          </div>
        </div>
      <div class="lt safe-intro-box">
          <div class="safe-intro-in-box">
            <div class="rol safe-intro-title">{{safe.p3.title}}</div>
            <div class="rol safe-intro-message">{{safe.p3.message}}</div>
            <img src="../../../assets/image/icon_green.png" class="safe-intro-icon" alt="">
          </div>
        </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_safe_intro",
    computed:{
      safe(){
        return this.$t('safe');
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .safe-intro-rol{
    @include property(background, #fff,#fafafa);
    padding-top:30px;
  }
  .safe-intro-box{
    @include property(width, 363px,660px);
    @include property(height, 188px,260px);
    @include property(margin-left, 0,20px);
    margin-bottom:50px;
    position:relative;
  }
  .safe-intro-in-box{
    position:absolute;
    box-sizing: border-box;
    @include property(width, 343px,640px);
    @include property(height, 168px,240px);
    padding: 25px 30px 0 30px;
    top:10px;
    left:10px;
    background: #FFFFFF;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    border-radius: 4px;
    transition:all .4s;
    &:hover{
      @include property(transform, scale(1.05),scale(1));
      border:1px solid rgba(120,202,243,0.5);
    }
  }
  .safe-intro-title{
    font-family: PingFangSC-Semibold;
    @include property(font-size, 22px,36px);
    color: #3B2CC1;
    font-weight:500;
    @include property(margin-bottom, 12px,28px);
    @include property(margin-top, 0,10px);
  }
  .safe-intro-message{
    font-family: PingFangSC-Light;
    @include property(font-size, 14px,25px);
    color: #333333;
    line-height: 1.6em;

  }
  .safe-intro-icon{
    position:absolute;
    @include property(width, 80px,120px);
    @include property(height, 80px,120px);
    @include property(bottom, -50px,-70px);
    right:20px;
  }
</style>


