<template>
  <div class="rol purse-rol" :class="{'en':lang==='en'}">
    <div class="m-box">
      <div class="rol purse-title">{{purse.title}}</div>
      <div class="rol purse-intro">
        {{purse.intro}}
      </div>
    </div>
    <div class="rol purse-message-bg">
      <div class="m-box">
        <div class="rol purse-message-body">
          <div class="lt purse-message-lt">
            <div class="rol purse-message-title">{{purse.inTitle}}</div>
            <div class="rol purse-message-font">{{purse.inIntro}}</div>
            <div class="rol purse-message-font" v-html="purse.message"></div>
            <div class="rol purse-download-btn-box">
              <a  :href="downloadUrl.bitpie.app"  class="lt banner-download-btn">
                <i class="lt fa banner-download-icon">&#xe901;</i>
                <div class="lt download-font-box">
                  <div class="rol download-font-title">Download on the</div>
                  <div class="rol download-font-name">App Store</div>
                </div>
              </a>
              <a :href="downloadUrl.bitpie.android" class="lt banner-download-btn" v-if="lang=='cn'">
                <i class="lt fa banner-download-icon android-icon">&#xe902;</i>
                <div class="lt download-font-box">
                  <div class="rol download-font-title">Download for</div>
                  <div class="rol download-font-name">Android</div>
                </div>
              </a>
              <a :href="downloadUrl.bitpie.google" class="lt banner-download-btn" v-else>
                <i class="lt fa banner-download-icon">&#xe903;</i>
                <div class="lt download-font-box">
                  <div class="rol download-font-title">Get it in</div>
                  <div class="rol download-font-name">Google Play</div>
                </div>
              </a>
            </div>
          </div>
          <div class="gt purse-message-gt">
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <div class="purse-message-img"></div>
            <img src="../../../assets/image/logo_main@2x.png" class="purse-logo" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_purse",
    computed:{
      lang(){
        return this.$store.state.lang;
      },
      purse(){
        return this.$t('purse');
      },
      downloadUrl() {
        return this.$store.state.downloadUrl;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  $hoverColor:#3B2CC1;
  .purse-rol{
    height:690px;
    @include property(height, 690px,1390px);
    padding:60px 0;
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    text-align: center;
  }
  .purse-title{
    font-family: PingFangSC-Medium;
    @include property(font-size, 36px,42px);
    @include property(font-weight, 400,500);
    @include property(margin-bottom, 25px,35px);
    color: #FFF;
  }
  .purse-intro{
    font-family: PingFangSC-Regular;
    @include property(padding, 0 200px,0 30px);
    @include property(font-size, 18px,25px);
    color:#fff;
    text-align: center;
    line-height:1.6em;
  }
  .purse-message-bg{
    height:345px;
    background:url("../../../assets/image/main_bg_diandian.png") repeat;
    background-size:30px;
    margin-top:70px;
  }
  .purse-message-body{
    height:384px;
    @include property(height, 384px,1070px);
    border-radius:10px;
    background:#fff;
    margin-top:-20px;
    padding:0 80px;
    @include property(padding, 0 80px,0 52px);
  }
  .purse-message-lt{
    width:570px;
    margin-right:50px;
  }
  .purse-message-title{
    font-family: PingFangSC-Semibold;
    @include property(font-size, 24px,30px);
    color: #3B2CC1;
    margin-top:60px;
    margin-bottom:30px;
    text-align: left;
    font-weight:bold;
  }
  .purse-message-font{
    @include property(font-size, 16px,25px);
    @include property(margin-bottom, 14px,0);
    color:#333;
    line-height:1.6em;
    text-align: left;
    &:last-child{
      @include property(margin-bottom, 14px,30px);
    }
  }
  .purse-download-btn-box{
    @include property(margin-top, 14px,30px);
    text-align: left;
  }
  .banner-download-btn{
    @include property(width, 162px,245px);
    @include property(height, 42px,70px);
    border:1px solid rgba(255,255,255,0.5);
    border-radius:42px;
    padding-left:18px;
    cursor:pointer;
    margin-right:20px;
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    /*transition:all 0.1s;*/
    &:last-child{
      margin-right:0;
      @include property(width, 165px,245px);
      .download-font-box{
        @include property(width, 110px,160px);
      }
    }
    &:first-child{
      @include property(width, 165px,245px);
    }
    &:hover{
      opacity: 0.8;
    }
  }
  .banner-download-icon{
    @include property(font-size, 24px,36px);
    color:#fff;
    @include property(line-height, 36px,62px);
  }
  .download-font-box{
    @include property(width, 103px,164px);
    margin-left:9px;
    @include property(margin-top, 4px,10px);
  }
  .download-font-title{
    font-family: PingFangSC-Regular;
    @include property(font-size, 9px,20px);
    @include property(margin-bottom, 0,2px);
    color:#fff;
    letter-spacing: 0;

  }
  .download-font-name{
    font-family: PingFangSC-Semibold;
    @include property(font-size, 18px,22px);
    color: #FFF;
    letter-spacing: 0;
    margin-top:1px;
  }
  .android-icon{
    @include property(font-size, 26px,41px);
  }
  .purse-message-gt{
    width:255px;
    height:255px;
    border-radius:50%;
    border:1px dashed rgba(0,0,0,0.2);
    margin-top:65px;
    margin-right:53px;
    @include property(margin-right, 53px,180px);
    @include property(margin-top, 65px,135px);
    @include property(transform, scale(1),scale(1.4));
    position:relative;
    text-align: center;

  }
  .purse-message-img{
    width:62px;
    height:62px;
    position:absolute;
    transition:all .1s ease-in .5s;;
    background-size:cover;
    &:nth-child(1){
      top:-31px;
      left:93px;
      background:url("../../../assets/image/a1.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b1.png") no-repeat;
        background-size:cover ;
      }

    }
    &:nth-child(2){
      top:6px;
      left:193px;
      background:url("../../../assets/image/a2.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b2.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(3){
      top:99px;
      left:226px;
      background:url("../../../assets/image/a3.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b3.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(4){
      top:189px;
      left:193px;
      background:url("../../../assets/image/a4.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b4.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(5){
      top:226px;
      left:93px;
      background:url("../../../assets/image/a5.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b5.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(6){
      top:189px;
      left:0;
      background:url("../../../assets/image/a6.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b6.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(7){
      top:99px;
      left:-34px;
      background:url("../../../assets/image/a7.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b7.png") no-repeat;
        background-size:cover ;
      }
    }
    &:nth-child(8){
      top:6px;
      left:0;
      background:url("../../../assets/image/a8.png") no-repeat;
      background-size:cover;
      &:hover{
        animation:p1 1s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
        background:url("../../../assets/image/b8.png") no-repeat;
        background-size:cover ;
      }
    }
    &:hover{
      /*transform: rotateY(180deg);*/
    }
  }
  .purse-logo{
    width:128px;
    height:128px;
    position:absolute;
    top:63px;
    left:63px;
  }
  @keyframes p1{
    0%{
      opacity: 1;
      transform: rotateY(0deg);
    }
    50%{
      /*opacity: 0;*/
      transform: rotateY(90deg);
    }
    100%{
      transform: rotateY(0deg);
      opacity: 1;
    }
  }
  .purse-rol.en{
    @include property(height, 690px,1570px);
    .purse-message-body{
      @include property(height, 384px,1150px);
    }
  }
</style>


