import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import news from '@/pages/news/news'
import publics from '@/pages/publics/publics'
import newsMessage from '@/pages/news_message/news_message'
import join from '@/pages/join/join'
import download from '@/pages/download/download'
import about from '@/pages/about/about'
import business from '@/pages/business/business'
import joinMessagePhone from '@/pages/join-message-phone/join_message'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/publics',
      name: 'publics',
      component: publics
    },
    {
      path: '/newsMessage',
      name: 'newsMessage',
      component: newsMessage
    },
    {
      path: '/join',
      name: 'join',
      component: join
    },
    {
      path: '/down',
      name: 'download',
      component: download
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/business',
      name: 'business',
      component: business
    },
    {
      path: '/joinMessagePhone',
      name: 'joinMessagePhone',
      component: joinMessagePhone
    },
  ]
})



