<template>
  <div class="rol money-rol" v-if="money">
    <div class="m-box" v-if="money.base_price">
      <div class="center-box">
        <div class="lt money-box money-btc">
          <div class="rol money-name-font">BTC/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_btc.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('BTC').current}}</span>
            <span class="gt money-percentage" :class="{'on':this.price('BTC').down}">{{this.price('BTC').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-eth">
          <div class="rol money-name-font">ETH/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_eth.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('ETH').current}}</span>
            <span class="gt money-percentage" :class="{'on':this.price('ETH').down}">{{this.price('ETH').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-eos">
          <div class="rol money-name-font">EOS/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_eos.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('EOS').current}}</span>
            <span class="gt money-percentage"  :class="{'on':this.price('EOS').down}">{{this.price('EOS').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-ltc">
          <div class="rol money-name-font">LTC/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_ltc.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('LTC').current}}</span>
            <span class="gt money-percentage"  :class="{'on':this.price('LTC').down}">{{this.price('LTC').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-bch">
          <div class="rol money-name-font">BCH/USD</div>
          <img class="money-icon" style="width:26px;height:16px;" src="../../../assets/image/icon_bch.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('BCH').current}}</span>
            <span class="gt money-percentage"  :class="{'on':this.price('BCH').down}">{{this.price('BCH').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-etc">
          <div class="rol money-name-font">ETC/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_etc.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('ETC').current}}</span>
            <span class="gt money-percentage"  :class="{'on':this.price('ETC').down}">{{this.price('ETC').percentage}}</span>
          </div>
        </div>
        <div class="lt money-box money-trx">
          <div class="rol money-name-font">TRX/USD</div>
          <img class="money-icon" src="../../../assets/image/icon_trx.png"/>
          <div class="rol money-message-rol">
            <span class="lt money-price">{{this.price('TRX').current}}</span>
            <span class="gt money-percentage" :class="{'on':this.price('TRX').down}">{{this.price('TRX').percentage}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "index_money",
    created:function(){
      this.$store.dispatch("GETMONEY");
    },
    computed:{
      money(){
        return this.$store.state.money;
      },
    },
    methods:{
      price(name){
        var obj={}
        obj.base=Math.floor(this.money.base_price[name]*100)/100;
        obj.current=Math.floor(this.money.current_price[name]*100)/100;
        obj.percentage=Math.floor((obj.current-obj.base)/obj.base*100)/100 + '%';
        obj.down=obj.current<obj.base;
        console.log(obj)
        return obj;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .money-rol{
    margin-bottom:30px;
    @include property(height, auto,290px);
    padding-top:4px;
    padding-bottom:4px;
    overflow:hidden;
  }
  @media screen and(max-width:1200px){
    .money-box:nth-child(2n){
      margin-right:0;
    }
    .money-box:last-child{
      display:none;
    }
    .money-box{
      margin-right:10px;
    }
  }
  .money-box{
    @include property(width, 145px,320px);
    @include property(height, 75px,130px);
    box-shadow: 0 1px 5px 0 rgba(0,0,0,0.10);
    background-size:100% auto !important;
    position:relative;
    padding:10px;
    border-radius:5px;
    @include property(margin-right, 13px,25px);
    @include property(margin-bottom, 0,25px);
    &:last-child{
      margin-right:0;
    }
    &:nth-child(2n){
       margin-right:13px;
     }
  }
  .money-name-font{
    font-family: PingFangSC-Light;
    @include property(font-size, 12px,25px);
    color: #999999;
    @include property(margin-top, 9px,25px);

  }
  .money-message-rol{
    @include property(margin-top, 5px,20px);
  }
  .money-price{
    font-family: PingFang SC;
    @include property(font-size, 20px,25px);
    color: #333333;
    @include property(line-height, 24px,36px);
    font-weight:bold;
  }
  .money-percentage{
    font-family: STYuanti-SC-Regular;
    @include property(font-size, 13px,25px);
    color: #43BD57;
    @include property(line-height, 24px,35px);
    /*margin-right:8px;*/
    &.on{
      color:#e96262;
    }
  }
  .money-icon{
    position:absolute;
    @include property(top, 12px,26px);
    right:10px;
    width:auto;
    @include property(height, 20px,32px);
  }
  .money-btc{
     background:url("../../../assets/image/btc_bg.png") no-repeat left bottom;
  }
  .money-eth{
    background:url("../../../assets/image/eth_bg.png") no-repeat left bottom;
  }
  .money-eos{
    background:url("../../../assets/image/eos_bg.png") no-repeat left bottom;
  }
  .money-ltc{
    background:url("../../../assets/image/ltc_bg.png") no-repeat left bottom;
  }
  .money-bch{
    background:url("../../../assets/image/bch_bg.png") no-repeat left bottom;
  }
  .money-etc{
    background:url("../../../assets/image/etc_bg.png") no-repeat left bottom;
  }
  .money-trx{
    background:url("../../../assets/image/trx_bg.png") no-repeat left bottom;
  }
  .center-box{
    @include property(padding-left, 0,20px);
  }
</style>


