<template>
  <div class="rol business-rol">
    <div class="rol business-banner">
      <div class="m-box">
        <div class="lt business-banner-lt">
          <div class="rol business-banner-title">Bitpie </div>
          <div class="rol business-banner-message"></div>
        </div>
        <div class="lt business-banner-gt">
          <img src="../../../assets/image/business_banner_pic.png" class="business-banner-img" alt="">
          <img src="../../../assets/image/feiji_xiao.png" class="feiji-img1" alt="">
          <img src="../../../assets/image/feiji_da.png" class="feiji-img2" alt="">
        </div>
      </div>
    </div>
    <div class="rol">
      <div class="m-box">
        <div class="rol input-rol">
          <div class="rol input-in-box">
            <div class="rol input-name"> /  <i>()</i></div>
            <div class="rol input-box">
              <input id="project_name" type="text" class="rol business-input">
            </div>
            <div class="rol input-name"> <i>()</i></div>
            <div class="rol input-box">
              <input id="project_url" type="text" class="rol business-input">
            </div>
            <div class="rol input-name">
              <br/>
              <span>()</span>
            </div>
            <div class="rol input-box">
              <input id="cord_name" type="text" class="rol business-input">
            </div>
            <div class="rol input-name"></div>
            <div class="rol input-box">
              <input id="convention" type="text" class="rol business-input">
            </div>
            <div class="rol input-name"> <i>()</i></div>
            <div class="rol input-box textarea-box">
              <textarea id="intro" v-model="intro"  class="rol business-textarea" maxlength="500"></textarea>
              <div class="fontLength-box">
                <span class="nowlength">{{nowLength}}</span> / <span class="maxLength">500</span>
              </div>
            </div>
            <div class="rol phone-title-rol"></div>
            <div class="rol input-name"> <i>()</i></div>
            <div class="rol input-box">
              <input id="name" type="text" class="rol business-name-input">
            </div>
            <div class="rol input-name"> <i>()</i></div>
            <div class="rol input-box">
              <input id="position" type="text" class="rol business-name-input">
            </div>
            <div class="rol input-name">
               <i>()</i><br/>
              <span> /  / </span>
            </div>
            <div class="rol input-box">
              <input id="phone" type="text" class="rol business-name-input">
            </div>
            <div class="rol input-btn-rol">
              <div class="input-btn"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "businessBody",
    data(){
      return {
        nowLength:0,
        intro:"",
      }
    },
    watch:{
      intro(){
        this.nowLength=this.intro.length;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .business-rol{
    padding-top:60px;
  }
  .business-banner{
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    @include property(height,520px,800px);
    /*padding-top:60px;*/
    margin-bottom:30px;
  }
  .business-banner-lt{
    width:500px;
    margin-top:100px;
  }
  .business-banner-title{
    font-family: PingFangSC-Regular;
    font-size: 48px;
    color: #FFFFFF;
    margin-bottom:50px;
  }
  .business-banner-message{
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 50px;
  }
  .business-banner-gt{
    position: relative;
    width:600px;
    margin-top:100px;
    &:hover{
      .feiji-img1{
        animation:img1 2s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
      }
      .feiji-img2{
        animation:img2 2.5s ease-in-out infinite alternate;
        animation-iteration-count:1;
        animation-fill-mode: forwards;
      }
    }
  }
  .input-rol{
    background: #FFFFFF;
    box-shadow: 0 0 8px 0 rgba(0,0,0,0.15);
    padding:20px;
    margin-bottom:100px;
  }
  .input-in-box{
    background-image: linear-gradient(225deg, #FFFFFF 0%, #E2EDFF 100%);
    padding:30px 100px 60px 100px;
  }
  .input-box{

  }
  .input-name {
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    margin-bottom:20px;
    span{
      font-size: 13px;
      color: #999999;
      letter-spacing: 0;
    }
    i{
      color:red;
    }
  }
  .business-input{
    height:40px;
    border:1px solid #eaeaea;
    background:#fff;
    margin-bottom:25px;
    outline:none;
    padding-left:20px;
    font-size:18px;
  }
  .business-name-input{
    height:40px;
    border:1px solid #eaeaea;
    background:#fff;
    margin-bottom:25px;
    outline:none;
    padding-left:20px;
    font-size:18px;
    width:420px;
  }
  .business-textarea{
    resize:none;
    height:185px;
    border:1px solid #eaeaea;
    background:#fff;
    outline:none;
    padding-left:20px;
    font-size:18px;
    line-height:1.4em;
    padding-top:10px;
  }
  .textarea-box{
    position:relative;
    margin-bottom:60px;
  }
  .phone-title-rol{
    font-size:14px;
    color:#999;
    padding-top:10px;
    border-top:1px dashed #a5b3c9;
    text-align: center;
    margin-bottom:40px;
  }
  .input-btn-rol{
    text-align: center;
  }
  .input-btn{
    width:215px;
    height:55px;
    display:inline-block;
    text-align: center;
    line-height:55px;
    background-image: linear-gradient(50deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    border-radius:60px;
    font-size:17px;
    color:#fff;
    cursor: pointer;
    margin-top:40px;
  }
  .business-banner-img{
    width:500px;
    @include property(margin-left,100px,70px);
  }
  .feiji-img1{
    width:44px;
    height:30px;
    position:absolute;
    top:200px;
    left:70px;
    opacity: 0;
    @include property(left,70px,20px);
  }
  .fontLength-box{
    position:absolute;
    bottom:10px;
    right:20px;
  }
  .feiji-img2{
    width:73px;
    height:49px;
    position:absolute;
    top:0px;
    left:650px;
    opacity: 0;
    @include property(left,650px,600px);
  }
  @keyframes img1{
    0% {
      top:300px;
      width:0;
      height:0;
      opacity: 0;
      left:-100px;
    }
    100% {
      width:44px;
      height:30px;
      position:absolute;
      top:200px;
      @include property(left,70px,20px);
      opacity: 1;
    }
  }
  @keyframes img2{
    0% {
      width:0;
      height:0;
      position:absolute;
      top:300px;
      left:950px;
      opacity: 0;
    }
    100% {
      width:73px;
      height:49px;
      position:absolute;
      top:0px;
      @include property(left,650px,600px);
      opacity: 1;
    }
  }
</style>


