<template>
  <div class="rol shield-intro" :class="{'en':lang==='en'}">
    <div class="m-box">
      <div class="rol shield-intro-rol">
        <div class="lt shield-message-box">
          <div class="rol shield-title">{{shield.title}}</div>
          <div class="rol shield-font" v-html="shield.message"></div>
        </div>
        <img src="../../../assets/image/pic_f@2x.png" class="shield-img pc-box" alt="">
        <img src="../../../assets/image/phone-shield-img.png" class="shield-img phone-box" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index_shield_intro",
    computed:{
      shield(){
        return this.$t('shield');
      },
      lang(){
        return this.$store.state.lang;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .shield-intro{
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    @include property(height, 516px,1000px);
    &.en{
      @include property(height, 516px,1050px);
    }
    .shield-intro-rol{
      @include property(height, 516px,1000px);
    }
    @include property(position, static,relative);
  }
  .shield-intro-rol{
    @include property(height, 516px,1000px);
    @include property(position,relative ,static);
  }
  .shield-message-box{
    @include property(width, 600px,100%);
    padding-left:40px;
  }
  .shield-title{
    font-family: PingFangSC-Regular;
    @include property(font-size, 32px,42px);
    @include property(font-weight, 400,500);
    @include property(margin-top, 60px,80px);
    color: #FFFFFF;
    margin-bottom:30px;
  }
  .shield-font{
    font-family: PingFangSC-Regular;
    @include property(font-size, 18px,28px);
    color:#fff;
    line-height:1.6em;
  }
  .shield-img{
    position:absolute;
    bottom:0;
    right:0;
    @include property(right, 0,-25px);
    @include property(width, 602px,750px);
    @include property(height, 456px,650px);

    width:602px;
    height:456px;
  }
  .phone-box{
    @include property(display,none,block);
  }
  .pc-box{
    @include property(display,block,none);
  }
</style>


