<template>
  <div class="rol download-rol">
    <div class="m-box">
      <div class="lt download-lt">
        <div class="rol download-font">Bither</div>
        <div class="rol download-font-se">a simple and secure Bitcoin Wallet!</div>
        <div class="rol banner-download">
          <a :href="downloadUrl.bither.ios" target="_blank"   class="lt banner-download-btn">
            <i class="lt fa banner-download-icon">&#xe901;</i>
            <div class="lt download-font-box">
              <div class="rol download-font-title">Download on the</div>
              <div class="rol download-font-name">App Store</div>
            </div>
          </a>
          <div class="lt banner-download-btn" @click="openAlert($event)">
            <i class="lt fa banner-download-icon android-icon">&#xe902;</i>
            <div class="lt download-font-box">
              <div class="rol download-font-title">Download for</div>
              <div class="rol download-font-name">Android</div>
            </div>
            <div class="download-alert-box">
              <img src="../../../assets/image/download_bg.png" class="downloadBg" alt="">
              <a :href="downloadUrl.bither.android" class="rol download-alert-a">Download</a>
              <a :href="downloadUrl.bither.google"  class="rol download-alert-a">Google Play</a>
            </div>
          </div>
          <div class="lt banner-download-btn" @click="openAlert($event)">
            <i class="lt fa banner-download-icon">&#xe620;</i>
            <div class="lt download-font-box">
              <div class="rol download-font-title">Download</div>
              <div class="rol download-font-name">Desktop</div>
            </div>
            <div class="download-alert-box">
              <img src="../../../assets/image/download_bg.png" class="downloadBg" alt="">
              <a :href="downloadUrl.bither.win64"   class="rol download-alert-a">Windows 64</a>
              <a :href="downloadUrl.bither.win32"   class="rol download-alert-a">Windows 32</a>
              <a :href="downloadUrl.bither.mac64"   class="rol download-alert-a">MacOSX 64</a>
              <a :href="downloadUrl.bither.linux"   class="rol download-alert-a">Linux 64</a>
            </div>
          </div>
        </div>
      </div>
      <div class="lt download-gt pc-box">
        <img src="../../../assets/image/bitcoin_banner_phone.png" v-if="lang === 'cn'" class="lt down-phone" alt="">
        <img src="../../../assets/image/download_en.png" v-if="lang === 'en'" class="lt down-phone" alt="">
        <img src="../../../assets/image/bitcoin_xiaoxin.png" alt="" class="down-phone-xin">
        <img src="../../../assets/image/bitcoin_banner_daxin.png" alt="" class="down-phone-dxin">
      </div>
      <div class="lt download-gt phone-box">
        <img src="../../../assets/image/bg@2x.png" class="lt down-phone" v-if="lang === 'cn'" alt="">
        <img src="../../../assets/image/bg_en_phone.png" class="lt down-phone" v-if="lang === 'en'" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "downloadBody",
    computed:{
      lang(){
        return this.$store.state.lang;
      },
      downloadUrl() {
        return this.$store.state.downloadUrl;
      }

    },
    methods:{
      openAlert(e){
        var is;
        var dom =$(e.target).hasClass("banner-download-btn")?$(e.target):$(e.target).parents(".banner-download-btn");

        dom.hasClass('on')?is=2:is=1;
        $(".banner-download-btn").removeClass("on");
        is==2?dom.removeClass("on"):dom.addClass("on");
      }
    },
    created(){
      $(document).click(function(e){

        var _con = $('.banner-download-btn');   // 

        if(!_con.is(e.target) && _con.has(e.target).length === 0){

          _con.removeClass("on");

        }

      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  $hoverColor:#3B2CC1;
  .download-rol{
    @include property(height, 700px,1050px);
    margin-top:60px;
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
  }
  .download-lt{
    @include property(margin-top, 100px,100px);
    @include property(width, 650px,700px);
    @include property(margin-right, 50px,0px);
    width:650px;
    margin-right:50px;
  }
  .download-font{
    font-size:50px;
    color:#fff;
    line-height:107px;
  }
  .download-font-se{
    font-size:36px;
    color:#fff;
    margin-bottom:55px;
  }
  .banner-download{

  }
  .banner-download-btn{
    @include property(width, 162px,245px);
    @include property(height, 42px,70px);
    border:1px solid rgba(255,255,255,0.5);
    border-radius:42px;
    padding-left:18px;
    cursor:pointer;
    margin-right:20px;
    position:relative;
    /*transition:all 0.1s;*/
    &:last-child{
      margin-right:0;
      @include property(width, 165px,180px);
      .download-font-box{
        @include property(width, 104px,110px);
      }
    }
    &:nth-child(2){
      @include property(width, 165px,220px);
      .download-font-box{
        @include property(width, 104px,135px);
      }
    }
    &:first-child{
      @include property(width, 165px,245px);
    }
    &:hover{
      background:#fff;
      color:#fff;
      .banner-download-icon{color:$hoverColor};
      .download-font-title{color:$hoverColor};
      .download-font-name{color:$hoverColor};
    }
    &.on{
      background:#fff;
      color:#fff;
      .banner-download-icon{color:$hoverColor};
      .download-font-title{color:$hoverColor};
      .download-font-name{color:$hoverColor};
      .download-alert-box{
        display:block;
      }
    }
  }
  .banner-download-icon{
    @include property(font-size, 24px,36px);
    color:#fff;
    @include property(line-height, 36px,62px);
  }
  .download-font-box{
    @include property(width, 104px,164px);
    margin-left:9px;
    @include property(margin-top, 4px,10px);
  }
  .download-font-title{
    font-family: PingFangSC-Regular;
    @include property(font-size, 9px,20px);
    @include property(margin-bottom, 0,2px);
    color:#fff;
    letter-spacing: 0;

  }
  .download-font-name{
    font-family: PingFangSC-Semibold;
    @include property(font-size, 18px,22px);
    color: #FFF;
    letter-spacing: 0;
    margin-top:1px;
  }
  .android-icon{
    @include property(font-size, 26px,41px);
  }
  .download-gt{
    width:400px;
    @include property(margin-top, 100px,80px);
    @include property(margin-left, 0,70px);
    position:relative;
    &:hover{
      .down-phone-xin{
        top:136px;
        left:372px;
        width:28px;
        height:31px;
        opacity: 1;
      }
      .down-phone-dxin{
        top:90px;
        left:400px;
        width:44px;
        height:48px;
        opacity: 1;
      }
    }
  }
  .down-phone{
    @include property(width, 400px,550px);

  }
  .down-phone-xin{
    position:absolute;
    top:136px;
    left:372px;
    width:28px;
    height:31px;
    opacity: 0;
    transition: all .28s ease-in .1s;
  }
  .down-phone-dxin{
    position:absolute;
    top:90px;
    left:400px;
    width:44px;
    height:48px;
    opacity: 0;
    transition: all .28s ease-in .2s;
  }
  .download-alert-box{
    position:absolute;
    top:60px;
    left:5px;
    @include property(width, 140px,230px);
    @include property(top, 60px,100px);
    @include property(left, 5px,-50px);
    box-sizing:border-box;
    padding:0 20px;
    background:#fff;
    border-radius:5px;
    display:none;
    z-index: 30000;
  }
  .download-alert-a{
    height:50px;
    @include property(font-size, 16px,26px);
    @include property(height, 50px,70px);
    @include property(line-height, 50px,70px);
    color:#2d49c8;
    text-align: left;
    border-bottom:1px solid #a6bee1;
    &:last-child{
      border-bottom:none;
    }
    &:hover{
      opacity: 0.6;
    }
  }
  .downloadBg{
    top:-9px;
    width:24px;
    height:10px;
    right:20px;
    position:absolute;
  }
  .pc-box{
    @include property(display,block,none);
  }
  .phone-box{
    @include property(display,none,block);
  }
</style>


