<template>
  <div class="rol about-body">
    <div class="m-box">
      <div class="rol about-box">
        <div class="rol about-in-box">
          <img src="../../../assets/image/tree1.png" class="tree" alt="">
          <img src="../../../assets/image/shu2.png" class="tree" alt="">
          <img src="../../../assets/image/shu3.png" class="tree" alt="">
          <div class="rol about-title">
            {{about.title}}
          </div>
          <div class="lt about-line"></div>
          <div class="rol about-message-font" v-for="item in about.message">
            {{item}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "aboutBody",
    computed:{
      about(){
        return this.$t('about')
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .about-body{
    padding-top:60px;
    height:900px;
    @include property(height, 900px,auto);
    background-image: linear-gradient(-229deg, #320786 0%, #3827BB 46%, #3093C9 100%);
  }
  .about-box{
    height:610px;
    @include property(padding, 20px,10px);
    @include property(height, 610px,auto);
    background:#fff;
    margin-top:140px;
    margin-bottom:40px;
  }
  .about-in-box{
    @include property(padding, 55px 170px,55px 40px 150px 40px);
    @include property(background-image,url("../../../assets/image/aboutbg.png"), linear-gradient(45deg, #fff 0%, #E2EDFF 100%));
    background-size:100% 100%;
    height:100%;
    position:relative;
  }
  .about-title{
    font-size:36px;
    color:#333;
    line-height:1em;
    margin-bottom:15px;
  }
  .about-line{
    width:63px;
    height:3px;
    background-image: linear-gradient(45deg, #2EA3C9 0%, #3A33CF 31%, #320685 100%);
    margin-bottom:50px;
  }
  .about-message-font{
    font-family: PingFangSC-Regular;
    @include property(margin-bottom, 10px,20px);
    @include property(font-size, 14px,28px);
    @include property(line-height, 1.6em,2em);
    line-height:1.6em;
    color:#333;
  }
  .tree{
    position:absolute;
    @include property(display, none,block);
    &:nth-child(1){
      top:20px;
      right:0;
      opacity: 1;
      width:90px;
    }
    &:nth-child(2){
      left:0;
      top:50%;
      margin-top:-40px;
      z-index: 1;
      opacity: 0.2;
    }
    &:nth-child(3){
      right:50px;
      bottom:0;
      z-index: 1;
      opacity: 0.5;
      width:100px;
    }
  }
</style>


