<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created(){
    var lang = this.$getLang();
    if(this.$getStorage("lang")){
      lang=this.$getStorage("lang");
    }
    this.$i18n.locale=lang;
    this.$store.commit('LANG',lang)
    var myDate = new Date()
    this.$store.commit('YEAR',myDate.getFullYear())
    this.$downloadUrl(this);


    var DEFAULT_WIDTH = 750, // 
      ua = navigator.userAgent.toLowerCase(), //  user agent 
      deviceWidth = window.screen.width, // 
      devicePixelRatio = window.devicePixelRatio || 1, // 1
      targetDensitydpi;

    // Android4.0viewportwidthtarget-densitydpi
    if (ua.indexOf("android") !== -1 && parseFloat(ua.slice(ua.indexOf("android")+8)) < 4) {
      targetDensitydpi = DEFAULT_WIDTH / deviceWidth * devicePixelRatio * 160;
      $('meta[name="viewport"]').attr('content', 'target-densitydpi=' + targetDensitydpi +
        ', width=device-width, user-scalable=no');
    }

  }


}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



