export default {
  GETNEWSITEM: (state, getters) => {
    var data=[];
    var newsIndex=getters.GETNEWSINDEX;
    var item=state.newsItem;
    var length=item.length;
    var index;
    for(var i = newsIndex;i<=newsIndex+3;i++){
      if(i<=length-1){
        data.push(item[i])
      }else{
        data.push(item[i-length]);
      }
    }
    if(newsIndex+3>=length-1){
      index= newsIndex + 4 - length;
    }else{
      index=newsIndex+4
    }
    console.log({
      newsIndex:index,
      item:data
    });
    return {
      newsIndex:index,
      item:data
    }
  },
  GETNEWSINDEX:state => {
    return state.newsIndex
  }
}


