<template>
  <div class="rol join-body" v-if="joinJson">
    <div class="m-box">
      <div class="rol join-title">
        {{join.title}}
      </div>
      <div class="rol join-yh-message">{{message}}</div>
      <div class="rol join-btn-rol join-padding">
        <div class="lt join-btn" @click="joinPlaceChange('bj')" v-if="lang=='cn'" :class="{'on':joinPlace=='bj'}">{{join.bjBtn}}</div>
        <div class="lt join-btn" @click="joinPlaceChange('hk')"   :class="{'on':joinPlace=='hk'||joinPlace=='hkEn'}">{{join.hkBtn}}</div>
      </div>
      <div class="rol join-message-body join-padding">
        <div v-for="item in joinJson[joinPlace]" @click="openAlert(item.id)" class="lt join-message-box">
          <span class="lt">{{item.name}}</span>
          <i class="gt fa">&#xe601;</i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "join_header",
    data () {
      return {
        message: this.joinMessage
      }
    },
    created(){
      var place;
      this.lang == 'en'? place = 'hkEn': place = 'bj'
      this.$store.commit("PLACECHANGE",place);
      this.$store.dispatch("GETJOINJSON");
      this.joinPlaceChange(place);
    },
    computed:{
      joinJson(){
        return this.$store.state.joinJson;
      },
      joinPlace(){
        return this.$store.state.joinPlace;
      },
      lang () {
        return this.$store.state.lang;
      },
      join () {
        return this.$t('join');
      },
      joinMessage : {
        get() {
          return this.join.joinBj
        },
        set(newValue) {
          this.message=newValue
        }

      }
    },
    methods:{
      openAlert(id){
        var winWidth;
        if (window.innerWidth) {
          winWidth = window.innerWidth;
        } else if ((document.body) && (document.body.clientWidth)) {
          winWidth = document.body.clientWidth;
        }
        console.log(winWidth);
        if(winWidth>=1200){
          this.$store.commit("ALERTOPEN",id);
          this.$bodyScroll(1);
        }else if(winWidth<1200){
          this.$router.push({
            path:'/joinMessagePhone',
            query:{
              "joinPlace":this.joinPlace,
              "joinId":id
            }
          })
        }

      },
      joinPlaceChange(id){
        var str;
        if(id=='bj'){
          str = this.join.joinBj
        }else if(id=='hk'||id=='hkEn'){
          str= this.join.joinHk
        }
        this.joinMessage=str;
        this.$store.commit("PLACECHANGE",id);
      }
    },
    watch:{
      lang (str) {
        if(str == 'en'){
          this.$store.commit("PLACECHANGE",'hkEn');
          this.joinPlaceChange('hkEn');
        }else if(str == 'cn'){
          this.$store.commit("PLACECHANGE",'bj');
          this.joinPlaceChange('hk');
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .join-body{
    background:#fafafa;
    @include property(padding,60px 0 220px 0,60px 0 50px 0);
  }
  .join-title{
    font-size:36px;
    @include property(font-size,36px,42px);
    @include property(font-weight,400,500);
    @include property(margin-bottom,30px,40px);
    text-align: center;
    color:#333;
  }
  .join-btn-rol{
    @include property(margin-bottom,80px,40px);
  }
  .join-btn{
    @include property(height,50px,65px);
    @include property(line-height,50px,65px);
    @include property(border-radius,50px,65px);
    @include property(font-size,20px,26px);

    width:auto;
    padding:0 40px;

    text-align: center;
    background:#fff;
    box-shadow: 2px 7px 12px 0 rgba(55,42,187,0.05);
    color:#333;
    margin-right:40px;
    cursor:pointer;
    &:hover{
      color:#fff;
      background:#2d49c6;
      font-weight:bold;
    }
    &.on{
      color:#fff;
      background:#2d49c6;
      font-weight:bold;
    }
  }
  .join-message-box{
    font-family: PingFangSC-Semibold;
    width:505px;
    @include property(width,505px,100%);
    height:93px;
    line-height:93px;
    padding:0 30px;
    background:#fff;
    box-shadow: 2px 7px 12px 0 rgba(55,42,187,0.05);
    margin-right:90px;
    margin-bottom:30px;
    cursor:pointer;
    @include property(font-size,22px,28px);
    font-weight:500;
    border-radius:10px;
    &:nth-child(2n){
      margin-right:0;
    }
  }
  .join-yh-title{
    font-size:18px;
    color:#2d49c6;
    margin-bottom:10px;
    margin-top:20px;
  }
  .join-yh-message{
    @include property(font-size,22px,28px);
    @include property(margin-bottom,45px,65px);
    text-align: center;
    color:#4a4a4a;
    line-height:1.6em;
  }
  .join-padding{
    @include property(padding,0,0 20px);
  }
</style>


