<template>
  <div class="rol">
    <index-header :topchange="false"></index-header>
    <index-banner></index-banner>
    <index-public class="phone-box"></index-public>
    <index-money></index-money>
    <index-public  class="pc-box"></index-public>
    <index-safe-intro></index-safe-intro>
    <index-purse></index-purse>
    <index-intro></index-intro>
    <index-shield-intro></index-shield-intro>
    <index-friends></index-friends>
    <index-news></index-news>
    <index-footer></index-footer>
  </div>

</template>

<script>
  import header from './components/index_header';
  import banner from './components/index_banner';
  import money from './components/index_money';
  import indexPublic from './components/index_public';
  import indexSafeIntro from './components/index_safe_intro';
  import indexPurse from './components/index_purse';
  import indexIntro from './components/index_intro';
  import indexShieldIntro from './components/index_shield_intro';
  import indexNews from './components/index_news';
  import indexFooter from './components/index_footer';
  import indexFriends from './components/index_friends';
  export default {
    name: "index",
    components: {
      indexHeader: header,
      indexBanner: banner,
      indexMoney: money,
      indexPublic:indexPublic,
      indexSafeIntro:indexSafeIntro,
      indexPurse:indexPurse,
      indexIntro:indexIntro,
      indexShieldIntro:indexShieldIntro,
      indexNews:indexNews,
      indexFooter:indexFooter,
      indexFriends:indexFriends
    },
    mounted () {
      var that = this;
      window.addEventListener('scroll', this.handleScroll);

      window.onresize = () => {
        return (() => {
          that.$store.commit("BODYWIDTH",document.documentElement.clientWidth);
        })()
      }

    },
    methods:{
      handleScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        this.$store.commit("SCROLLTOP",scrollTop);
      },
    }
  }
</script>

<style scoped lang="scss">
  @import "../../style/pie";
  .phone-box{
    @include property(display,none,block);
  }
  .pc-box{
    @include property(display,block,none);
  }
</style>


