  <div class="rol">
    <index-header :topchange="true"></index-header>
    <join-banner></join-banner>
    <join-body></join-body>
    <join-alert></join-alert>
    <index-footer></index-footer>

  </div>
</template>

<script>
  import header from '../index/components/index_header';
  import indexFooter from '../index/components/index_footer';
  import joinBanner from './components/join_banner';
  import joinBody from './components/join_body';
  import joinAlert from './components/join_alert';
  export default {
    name: "join",
    components: {
      indexHeader:header,
      indexFooter:indexFooter,
      joinBanner:joinBanner,
      joinBody:joinBody,
      joinAlert:joinAlert,
    },
  }
</script>

