<template>
  <div class="rol publics-item-rol">
    <div class="m-box">
      <div class="rol publics-item-box">
        <div class="rol publics-btn-rol">
          <div class="lt publics-btn" @click="publicsClick(0)" :class="{'on':publicsBtn=='0'}">{{announcement.all}}</div>
          <div class="lt publics-btn" @click="publicsClick(1)" :class="{'on':publicsBtn=='1'}">{{announcement.notices}}</div>
          <div class="lt publics-btn" @click="publicsClick(2)" :class="{'on':publicsBtn=='2'}">{{announcement.activities}}</div>
          <div class="lt publics-btn" @click="publicsClick(3)" :class="{'on':publicsBtn=='3'}">{{announcement.other}}</div>
        </div>
        <div class="rol publics-item"   @click="itemClick(item.id)" v-for="item in publicsItem" v-if="item.type==publicsBtn||publicsBtn==0">
          <div class="lt publics-data-box">
            <div class="rol publics-data-font">{{item.day}}</div>
            <div class="rol publics-data-month">{{item.month}}</div>
          </div>
          <div class="lt publics-message-box">
            <div class="rol publics-title-font"  style="-webkit-box-orient: vertical;">{{item.title}}</div>
            <div class="rol publics-message-intro" style="-webkit-box-orient: vertical;">{{item.intro}}</div>
            <div class="rol publics-readmore"  @click="itemClick(item.id)">{{announcement.view}}</div>
          </div>
        </div>
        <!--<div class="rol pagination-rol">-->
          <!--<el-pagination class="pagination"-->
                         <!--background-->
                         <!--layout="prev, pager, next"-->
                         <!--:total="publicsItem.length"-->
                         <!--@current-change="pageChange"-->
          <!--&gt;-->
          <!--</el-pagination>-->
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "publics_item",
    data () {
      return {
        publicsBtn:0
      }
    },
    methods: {
      pageChange(currentPage) {
        console.log(currentPage);
      },
      itemClick(id){
        this.$router.push({path:'/newsMessage',query:{"id":id}});

      },
      publicsClick(type){
        this.publicsBtn=type;
      }
    },
    created(){
      this.$store.dispatch("PUBLICSITEM")
    },
    computed:{
      publicsItem(){
        return this.$store.state.publicsItem;
      },
      lang(){
        return this.$store.state.lang;
      },
      announcement(){
        return this.$t('announcement');
      }
    },
    watch:{
      lang(){
        this.$store.dispatch("PUBLICSITEM");
      }
    }

  }
</script>

<style scoped lang="scss">
  @import "../../../style/pie";
  .publics-item-rol{
    @include property(padding-top,80px,140px);
  }
  .publics-item-box{
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10);
    @include property(padding, 30px 80px,30px 30px);
    margin-bottom:100px;
  }
  .publics-item{
    @include property(height, 155px,255px);
    border-bottom:1px dashed rgba(200,200,200,0.5);
    padding:40px 0;
    cursor:pointer;
    &:last-child{
      border-bottom:none;
    }
  }
  .publics-data-box{
    width:70px;
    /*border-top:4px solid #2D49C6;*/
    @include property(margin-right, 70px,40px);
    @include property(padding-top, 0,20px);
    @include property(width, 70px,80px);
  }
  .publics-data-font{
    font-size:48px;
    @include property(font-size, 48px,52px);
    line-height:1em;
    margin-bottom:10px;
    text-align: center;
    color:#666;
  }
  .publics-data-month{
    @include property(font-size, 16px,25px);
    color:#666;
    text-align: center;
  }
  .publics-message-box{
    @include property(width, 800px,520px);

  }
  .publics-title-font{
    @include property(font-size, 22px,30px);
    @include property(margin-bottom, 10px,20px);
    color:#333;
    line-height:1em;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    height:30px;
  }
  .publics-title-data{
    @include property(font-size, 13px,24px);
    color:#999;
    margin-bottom:10px;
  }
  .publics-message-intro{
    @include property(font-size, 14px,26px);
    @include property(height, 43px,80px);
    color:#666;
    line-height:1.6em;
    @include property(margin-bottom, 15px,20px);
    @include property(-webkit-line-clamp, 2,2);
    display: -webkit-box;
    // eslint-disable-next-line
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */

    overflow: hidden;
  }
  .publics-readmore{
    @include property(font-size, 12px,24px);
    color:#2d49c6;
    cursor:pointer;
    text-align: right;
  }
  .pagination-rol{
    text-align: center;
    margin-top:30px;
  }
  .pagination{
    display: inline-block;
  }
  .publics-btn-rol{
    @include property(height, 50px,80px);
    margin-top:5px;
    border-bottom:1px dashed rgba(200,200,200,0.5);
  }
  .publics-btn{
    @include property(font-size, 20px,31px);
    @include property(margin-right, 80px,40px);
    color:#333;
    margin-right:80px;
    @include property(height, 35px,54px);
    line-height:1.6em;
    cursor:pointer;
    &:hover{
      border-bottom:3px solid #2d49c6;
      color:#2d49c6;
      font-weight:500;
    }
    &.on{
      border-bottom:3px solid #2d49c6;
      color:#2d49c6;
      font-weight:500;
    }
  }
</style>


